@import "https://fonts.googleapis.com/css?family=Biryani|Fjalla+One";

.lower {
  width: 100%;
  opacity: 0.8;
  height: 50px;
  float: right;
}

.lower:hover {
  color: white;
}

.hide {
  display: none;
}

.lower label {
  font-family: "Biryani";
  text-transform: uppercase;
  font-size: 50px;
  text-align: center;
  color: #893843;
}

.lower label:hover {
  cursor: pointer;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.9);
}

.buttonred {
  float: right;
  width: 50px;
  height: 50px;
  position: absolute;
  right: 20px;
  top: 20px;
}

.overlay label {
  width: 50px;
  height: 50px;
  position: relative;
  right: 26px;
  top: 14px;
  background: url("../../components/images/buttonwhite.png");
  z-index: 100;
  cursor: pointer;
  float: right;
}

.overlay nav {
  text-align: center;
  position: relative;
  top: 50%;
  height: 50%;
  font-size: 60px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.overlay ul {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  display: inline-block;
  height: 100%;
  position: relative;
}

.overlay ul li {
  display: block;
  height: 20%;
  height: calc(100% / 5);
  min-height: 54px;
}

.overlay ul li a {
  font-weight: 300;
  display: block;
  color: white;
  text-decoration: none;
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
  font-family: "Biryani";
  text-transform: uppercase;
}

.overlay ul li a:hover,
.overlay ul li a:focus {
  color: #893843;
}

.lower ~ .overlay-hugeinc {
  opacity: 0;
  visibility: hidden;
  -webkit-transition: opacity 0s, visibility 0s;
  transition: opacity 0s, visibility 0s;
}

#op:checked ~ .overlay-hugeinc {
  opacity: 1;
  visibility: visible;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.overlay-hugeinc nav {
  -moz-perspective: 300px;
}

.overlay-hugeinc nav ul {
  opacity: 0.4;
  -webkit-transform: translateY(-25%) rotateX(35deg);
  transform: translateY(-25%) rotateX(35deg);
  -webkit-transition: -webkit-transform 0.5s, opacity 0.5s;
  transition: transform 0.5s, opacity 0.5s;
}

#op:checked ~ .overlay-hugeinc nav ul {
  opacity: 1;
  -webkit-transform: rotateX(0deg);
  transform: rotateX(0deg);
}

#op:not(:checked) ~ .overlay-hugeinc nav ul {
  -webkit-transform: translateY(25%) rotateX(-35deg);
  transform: translateY(25%) rotateX(-35deg);
}

.navbar {
  position: relative;
  width: 100%;
  height: 0px;
  z-index: 10;
  display: flex;
  justify-content: right;
  margin-top: 0px;
  background-color: rgba(0, 0, 0, 0.9);
}

.p1 {
  display: hidden;
}
